<template>
	<div class="main_con">
		
		<div class="local">
		当前位置：<span class="pos1">麻阳河国家级自然保护区</span> > <span class="pos2">{{ $route.name}}</span>
	</div>
	<div class="search">
		    <div class="del"><span></span>删除</div>
		    <div class="update"><span></span>修改</div>
		    <div class="add"><span></span>增加</div>
		    <div class="input">
			    <input type="text" placeholder="请输入查询条件" />
			    <div id="icon"></div>
			</div>
	</div>
	<div class="page_list">
		<table class="msg_table">
			            				<thead>
			            					<tr>
			            						<td>
			            							<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
			            							</div>
			            						</td>
			            						<td v-for="i in thead">{{i}}</td>
			            					</tr>
			            				</thead>
			            				<tbody>
			            					<ttboady v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="item in tr" v-bind:todo="item" v-bind:key="item.id"></ttboady>
			            				</tbody>
			            		</table>
			            				<ul class="page">
			            					<li>共<span class="total_page">7</span>页，
			            						每页<span class="page_num">10</span>条，
			            						共<span class="total_num">100</span>条
			            					</li>
			            					<li>
			            						<div class="top"></div>
			            					</li>
			            					<li><div class="prev"></div></li>
			            					<li>
			            						<input type="text" class="local_page" value="1"/>
			            					</li>
			            					<li><div class="next"></div></li>
			            					<li><div class="bottom"></div></li>
			            					<li><div class="gon">GO</div></li>
			            				</ul></div>
	</div>
	
</template>

<script>
 import ttboady from './page/comments/table-tbody'
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["手机唯一标识","设备名","备注","审核状态","操作"],
		    					tr:[
										["12312",'454898',"1454564","3432","46549871"],	
										["4d3ef",'dfdgre',"fdt","ereff","feff"],	
										["fe3",'fev',"wefr","23","efe3"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
									],
		    					isA:false,
		    					isfA:false,
								}
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	methods:{
		    		check_box:function(){
		    			
		    			this.isA = !this.isA;
		    			this.isfA = !this.isfA;
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		}
		    	}
}
</script>

<style scoped="scoped">

	
/**********当前位置**********/
.main .table{
	margin-bottom:0px;
}
.main div.search{
	color:#fff;
	height:2.5em;
	padding-right:0.5em;
}
.main div.search>div{
	float:right;
	width:4.5em;
	height:2em;
	border-radius: 5px;
	background-color: rgba(205,210,234,0.2);
	margin-left:10px;
	line-height: 2em;
	cursor: pointer;
	border:1px solid #8a92a8;
}
.main div.search>div.input{
	width:22.5em;
	position:relative;
}
.main div.search input{
	width:22.6em;
	outline: none;
	background: none;
	border:none;
	color:#fff;
	padding-left:1em;
}
.main div.search div.icon{
	position:absolute;
	height:2em;
	width:2em;
	background-image: url(../assets/Manage/search.png);
	background-size: contain;
	top:0px;
	right:0px;
}
.main div.search input::-webkit-input-placeholder {
  color: #fff;
}
.main div.search input:-moz-placeholder {/* Firefox 18- */
  color: #fff;
}
.main div.search input::-moz-placeholder{/* Firefox 19+ */
 color: #fff;
}
.main div.search input:-ms-input-placeholder {
  color: #fff;
}
.main div.search input:-o-input-placeholder {
  color: #fff;
}
.main div.search div>span{
	display: block;
	float:left;
	height:22px;
	width:22px;
	margin-top:5px;
	margin-left:5px;
}
.main div.search div.del>span{
	background-image: url(../assets/Manage/del.png);
}
.main div.search div.add>span{
	background-image: url(../assets/Manage/add.png);
}
.main div.search div.update>span{
	background-image: url(../assets/Manage/update.png);
}
/***********表格样式*************/
.main .main_con div.table{
	padding:10px;
	background-color: #1f2e3a;
}

</style>


