		            				<div class="more"></div>
		            			</div>
		            			<div id="Log_manage">
		            				
		            			</div>
		            		</li>
		            		<li>
		            			<div class="top">
		            				<span>
		            					公告
		            				</span>
		            				<div class="more"></div>
		            			</div>
		            			<div id="Notice">
		            				<h2>公&nbsp;&nbsp;&nbsp;&nbsp;告</h2>
		            				<p>
		            					ECharts 工作在用户指定高宽的 DOM 节点（容器）中。ECharts 的组件和系列都在这个 DOM 节点中，每个节点都可以由用户指定位置。图表库内部并不适宜实现 DOM 文档流布局，因此采用类似绝对布局的简单容易理解的布局方式。但是有时候容器尺寸极端时，这种方式并不能自动避免组件重叠的情况，尤其在移动端小屏的情况下。
										另外，有时会出现一个图表需要同时在PC、移动端上展现的场景。这需要 ECharts 内部组件随着容器尺寸变化而变化的能力。为了解决这个问题，ECharts 完善了组件的定位设置，并且实现了类似 CSS Media Query 的自适应能力。
		            				</p>
		            			</div>
		            		</li>
		            		<li class="Weekly_wearning">
		            			<div class="top">
		            				<span>
		            					日志操作
