<script>
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入折线图组件
require('echarts/lib/chart/line')
// 引入饼状图组件
require('echarts/lib/chart/pie')

// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
  name: 'hello',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
         var myChart = echarts.init(document.getElementById('Log_manage'));
				    var font_color="#fff";
			myChart.setOption({
				   /* title: {
				        text: '示例',
				        left: 'center',
				        textStyle:{
				        	 color:"#fff",
				        }
				    },*/
				    tooltip: {
				        trigger: 'item',
				        formatter: '{a} <br/>{b} : {c}'
				    },
				   /* legend: {
				        left: 'left',
				        data: ['指数2',],
				        textStyle:{
				        	 color:"#fff",
				        }
				    },*/
				    xAxis: {
				        type: 'category',
				        name: 'x',
				        splitLine: {show: false},
				        color:"#fff",
				        data: ['一', '二', '三', '四', '五', '六', '七', '八', '九'],
					    nameTextStyle:{color:"#8fa6b3",},
				        axisLine:{
					    	lineStyle:{
					    		width:2,
					    		color:"#8fa6b3",
					    		type:"dotted",
					    		opacity:0.6,
					    	},
				    	},
				    },
				   
				    grid: {
				        left: '3%',
				        right: '4%',
				        bottom: '6%',
				        containLabel: true,
				        borderColor:"#8fa6b3",
				    },
				    yAxis: {
				        type: 'log',
				        name: 'y',
				        nameTextStyle:{
				        	 color:"#8fa6b3",
				        },
				         axisLine:{
					    	lineStyle:{
					    		width:2,
					    		color:"#8fa6b3",
					    		type:"dotted",
					    		opacity:0.6,
					    	},
					   
				    	},
				    },
				    series: [
				        {
				            name: '指数2',
				            type: 'line',
				            data: [28, 3, 9, 27, 81, 247, 124, 283, 369],
				            textStyle:{color:"#8fa6b3"},
