												    }],
												    globalCoord: true // 缺省为 false
											}
					            	}
					        },
				        },
				    ]
				    });

				      var myChart2 = echarts.init(document.getElementById('con'));
				    myChart2.setOption({
				    title: {
				        text: '系统登录点统计',
				        left: 'center',
				        textStyle:{
				        	 color:"#fff",
				        }
				    },
				
				    tooltip: {
		                trigger: 'item',
		                formatter: "{a} <br/>{b} : {c} ({d}%)"
		            },
					legend: {
				        orient: 'vertical',
				        left: 'left',
				        bottom:"bottom",
				        data: ['成都','武汉','郑州','北京','甘孜阿坝'],
				        textStyle:{
				        	color:"#eee",
				        }
				    },
		            series: [
		                {
		                    name: "登录统计",
					        type: 'pie',
					        radius : '70%',
