		                    },
		                    labelLine: {
		                        normal: {
		                            position: "outside",
		                            lineStyle: {
		                                color: '#b1b1b1'
		                            },
		                            //smooth: 0.2,
		                            length: 20,
		                            length2: 30
		                        }
		                    },
		                    itemStyle: {
		                        normal: {
		                            //color: '#c23531',
		                            //shadowBlur: 200,
		                            shadowColor: 'rgba(0, 0, 0, 0.5)'
		                        }
		                    }
		                }
		            ]
				    }),
				    
			window.onresize = function(){
			    myChart.resize(); 
			    myChart2.resize(); 
			};
    }
  }
}

	
