<template>
	<div class="main_con">
		
		<div class="local">
		当前位置：<span class="pos1">麻阳河国家级自然保护区</span> > <span class="pos2">{{ $route.name}}</span>
	</div>
	<div class="search">
		
		 <label for="">开始时间</label>  
			<el-date-picker  
			   class="reset-el-26 maxw-120"  
			   v-model="startDate"  
			   align="right"  
			   type="date"  
			   placeholder="选择日期"  
			   ref="picker1"  
			   :editable="canEdit"  
			   :picker-options="pickerOptions1">  
			 </el-date-picker>  
		 <label for="">结束时间</label>  
			 <el-date-picker  
			  class="reset-el-26 maxw-120"  
			  v-model="endDate"  
			  type="date"  
			  placeholder="选择日期"  
			  ref="picker2"  
			  :editable="canEdit"  
			  :picker-options="pickerOptions0">  
			</el-date-picker> 
		 用户：
		  <el-select v-model="value2" placeholder="请选择">
		    <el-option
		      v-for="item in options2"
		      :key="item.value"
		      :label="item.label"
		      :value="item.value"
		      :disabled="item.disabled">
		    </el-option>
  		</el-select>
		<el-button>查询按钮</el-button>
	</div>
	<div class="page_list">
		<table class="msg_table">
			     <thead>
			        <tr>
			            <td>
			            	<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
			            	</div>
			            </td>
			            <td v-for="i in thead">{{i}}</td>
			        </tr>
			     </thead>
			     <tbody>
			            <ttboady v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="item in tr" v-bind:todo="item" v-bind:key="item.id"></ttboady>
			      </tbody>
			      </table>
			        <ul class="page">
			            					<li>共<span class="total_page">7</span>页，
			            						每页<span class="page_num">10</span>条，
			            						共<span class="total_num">100</span>条
			            					</li>
			            					<li>
			            						<div class="top"></div>
			            					</li>
			            					<li><div class="prev"></div></li>
			            					<li>
			            						<input type="text" class="local_page" value="1"/>
			            					</li>
			            					<li><div class="next"></div></li>
			            					<li><div class="bottom"></div></li>
			            					<li><div class="gon">GO</div></li>
			            				</ul>
		</div>
	</div>
	
</template>

<script>
 import ttboady from './page/comments/table-tbody'
 
 let vueIns = null  
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["手机唯一标识","设备名","备注","审核状态","操作"],
		    					tr:[
										["12312",'454898',"1454564","3432","46549871"],	
										["4d3ef",'dfdgre',"fdt","ereff","feff"],	
										["fe3",'fev',"wefr","23","efe3"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
										["23efd",'234dad',"ddd","ffe","ewwfewf"],	
									],
		    					isA:false,
		    					isfA:false,
		    					pickerOptions0: {  
							        disabledDate(time) {  
							          // 最多只能选择一年的  
							          let ayearAgo = Date.now() - 31536000000  
							          return /*time.getTime() > Date.now() || */time.getTime() < ayearAgo  || (vueIns.startDate && vueIns.startDate.getTime && (time.getTime() < vueIns.startDate.getTime()))  
							        }  
							      },  
							    pickerOptions1: {  
							        disabledDate(time) {  
							          // 最多只能选择一年的  
							          let ayearAgo = Date.now() - 31536000000  
							          return time.getTime() > Date.now() || time.getTime() < ayearAgo || (vueIns.endDate && vueIns.endDate.getTime && (time.getTime() > vueIns.endDate.getTime()))  
							        }  
							       },
							    value1: '',
							    startDate: null,
							    endDate: null,
							    canEdit:true,
							    options2: [{
						          value: '选项1',
						          label: '黄金糕'
						        }, {
						          value: '选项2',
						          label: '双皮奶',
						          disabled: true
						        }, {
						          value: '选项3',
						          label: '蚵仔煎'
						        }, {
						          value: '选项4',
						          label: '龙须面'
						        }, {
						          value: '选项5',
						          label: '北京烤鸭'
						        }],
						        value2: ''
							  }
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	created(){
		    		vueIns=this;
		    	},
		    	methods:{
		    		check_box:function(){
		    			
		    			this.isA = !this.isA;
		    			this.isfA = !this.isfA;
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		}
		    	}
}
</script>

<style scoped="scoped">
	
div.search label{
	font-weight:normal ;
}	
.main div.local,div.search{
	color:#b9cdd7;
	padding-left:1em;
	margin-bottom: 8px;
}
/***********表格样式*************/
.main .main_con div.table{
	padding:10px;
	background-color: #1f2e3a;
}

</style>


